<template>
  <div class="CD">
    <div class="top">
      <a @click="$router.back()"><i class="fa fa-angle-down"></i></a>
      <div class="middle">
        <!-- <p class="songName">{{ song?.name }}</p> -->
        <marquee class="songName" scrollamount="3px">{{ song?.name }}</marquee>
        <p class="artist" v-if="song?.ar">
          {{ song?.ar[0]?.name }}<i class="fa fa-angle-right"></i>
        </p>
        <p class="artist" v-else>
          {{ song?.song?.artists[0]?.name }}<i class="fa fa-angle-right"></i>
        </p>
      </div>
      <div class="share">
        <img
          v-if="song?.picUrl"
          :src="`${song.picUrl}?imageView=1&type=webp&thumbnail=30x30`"
          alt=""
        />
        <img
          v-else
          :src="`${song?.al?.picUrl}?imageView=1&type=webp&thumbnail=30x30`"
          alt=""
        />

        <!-- <img v-else :src="song?.al?.picUrl" alt="" /> -->

        <!-- "song ? `${song.picUrl}?imageView=1&type=webp&thumbnail=30x0` : ''" -->

        <i>
          <img src="@/assets/24gl-share2.png" alt="" />
        </i>
      </div>
    </div>
    <div class="volume" v-show="isDetail">
      <i class="volume-icon">
        <img
          src="@/assets/volume.png"
          alt=""
          @click="
            $emit('change-volume', 0), (muted = true), changeVolumeSlider(0)
          "
          v-if="!muted"
        />
        <img
          src="@/assets/volume_close.png"
          alt=""
          v-if="muted"
          @click="
            $emit('change-volume', 100),
              (muted = false),
              changeVolumeSlider(100)
          "
        />
      </i>
      <div class="out">
        <input
          ref="input"
          type="range"
          min="0"
          value="100"
          @input="
            $emit('change-volume', $event.target.value);
            changeVolumeSlider($event.target.value);
          "
        />
        <div class="slider" ref="volumeSlider">
          <i class="dot"></i>
        </div>
      </div>
    </div>
    <div class="cd" v-show="!isDetail">
      <div class="al">
        <img src="@/assets/needle-ab.png" alt="" :class="{ active: playing }" />
      </div>
      <div class="center">
        <img src="@/assets/pan.png" alt="" class="pan" />
        <!-- <img
          src="http://p1.music.126.net/9UeioS5MWW9EuinCOU_RJA==/109951167897845387.jpg"
          alt=""
          class="pic"
          :class="{ active: playing }"
          @click="$emit('song-pause')"
        /> -->

        <img
          class="pic"
          @click="$emit('song-pause')"
          v-if="song?.picUrl"
          :src="`${song.picUrl}?imageView=1&type=webp&thumbnail=165x165`"
          alt=""
          :class="{ active: playing }"
        />
        <img
          class="pic"
          :class="{ active: playing }"
          @click="$emit('song-pause')"
          v-else
          :src="`${song?.al?.picUrl}?imageView=1&type=webp&thumbnail=165x165`"
          alt=""
        />

        <img
          src="@/assets/pause.png"
          class="pause"
          alt=""
          v-if="!playing"
          @click="$emit('song-play')"
        />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    song: Object,
    playing: Boolean,
    isDetail: Boolean,
  },
  data() {
    return {
      muted: false,
    };
  },
  created() {
    // console.log(123);
    // console.log(this.$parent.$router);
  },
  methods: {
    changeVolumeSlider(w) {
      this.$refs.volumeSlider.style.width = w + "%";
    },
  },
};
</script>

<style lang="less" scoped>
.volume {
  width: 100%;
  position: absolute;
  height: 60rem;
  display: flex;
  // justify-content: center;
  align-items: center;
  top: 45rem;
  padding-left: 43rem;
  i.volume-icon {
    position: relative;
    display: block;
    width: 30rem;
    height: 30rem;
    // background-color: #000;
    margin-right: 12rem;
    margin-left: 10rem;
    img {
      position: absolute;
      width: 100%;
      height: 100%;
    }
  }

  .out {
    width: 50%;
    position: relative;

    // left: 50%;
    // transform: translateX(-50%);
    height: 2rem;
    background-color: rgb(149, 145, 145);
    input {
      position: absolute;
      height: 5rem;
      width: 100%;
      opacity: 0;
    }
    .slider {
      // width: 50%;
      height: 100%;
      background-color: #fff;
      position: relative;
      i.dot {
        position: absolute;
        display: block;
        width: 5rem;
        height: 5rem;
        background-color: #fff;
        top: -2rem;
        border-radius: 50%;
        right: 0;
      }
    }
  }
}
.CD {
  color: rgba(255, 255, 255, 0.716);

  .top {
    display: flex;
    justify-content: space-between;
    box-sizing: border-box;
    padding: 10rem;
    padding-bottom: 35rem;

    a {
      width: 70rem;
      i {
        font-size: 30rem;
        color: #fff;
      }
    }
    .middle {
      text-align: center;
      width: 180rem;

      marquee.songName {
        font-size: 18rem;
        color: #fff;
      }
      p.artist {
        font-size: 15rem;
        display: flex;
        width: 100%;
        align-items: center;
        justify-content: center;
        margin-top: 3rem;

        i {
          display: inline-block;
          width: 20rem;
          height: 20rem;
          line-height: 20rem;

          font-size: 15rem;
          border-radius: 5rem;

          color: #fff;
          // background: rgba(214, 212, 212, 0.204);
        }
      }
    }
    .share {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 70rem;
      img {
        width: 30rem;
        height: 30rem;
        border-radius: 50%;
      }
      i {
        display: block;
        width: 30rem;
        height: 30rem;
        img {
          display: block;
          width: 100%;
          height: 100%;
          margin: 0 10rem;
          border-radius: 0;
        }
      }
    }
  }
  .cd {
    position: relative;
    // height: 300rem;
    // background-color: rgba(0, 0, 0, 0.8);
    text-align: center;
    // padding-top: 30rem;

    .al {
      height: 120rem;
      position: absolute;
      left: 52%;
      transform: translateX(-40%);
      z-index: 9;
      img {
        height: 100%;
        transform-origin: 10rem 10rem;
        transform: rotate(-20deg);
        transition: all 0.6s;
      }
      img.active {
        transform: rotate(-5deg);
      }
    }
    .center {
      width: 260rem;
      height: 260rem;
      margin: 0 auto;
      position: relative;
      //   background-color: pink;
      padding-top: 70rem;
      display: flex;
      justify-content: center;
      align-items: center;
      img {
        width: 165rem;
        height: 165rem;
        position: absolute;
        border-radius: 50%;
      }
      img.pic {
        animation: zhuan 5s linear infinite paused;

        // animation-iteration-count: infinite;
      }
      img.active {
        animation: zhuan 5s linear infinite;
      }
      img.pan {
        width: 265rem;
        height: 265rem;
      }
      img.pause {
        width: 60rem;
        height: 60rem;
      }
    }
  }
}

@keyframes zhuan {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
</style>