<template>
  <div id="app">
    <router-view
      @updata-song="song = $event"
      :song="song"
      :playing="playing"
      @song-play="$refs.audio.play()"
      @song-pause="$refs.audio.pause()"
      :duration="duration"
      :currentTime="currentTime"
      @click.native="showPlayPage = true"
      @update-time="$refs.audio.currentTime = $event"
      :lyris="lyris"
      @add-song="addSong($event)"
      @update-song-list="songlist = $event"
      :model="model"
      :models="models"
      @changeModel="changeModel"
      @changeSong="changeSong"
      :style="{
        marginBottom:
          this.$route.meta.showControls && songlist.length ? '65rem' : '0rem',
      }"
      :songlist="songlist"
      @delete-song="deleteSong"
      @change-volume="changeVolume"
    >
    </router-view>
    <audio
      :src="` https://music.163.com/song/media/outer/url?id=${song?.id}.mp3`"
      autoplay
      ref="audio"
      @play="playing = true"
      @pause="playing = false"
      @durationchange="duration = $event.target.duration"
      @timeupdate="currentTime = $event.target.currentTime"
      @ended="changeSong('next')"
    ></audio>

    <MusicsControl
      v-show="songlist.length"
      :song="song"
      :playing="playing"
      @song-play="$refs.audio.play()"
      @song-pause="$refs.audio.pause()"
      :duration="duration"
      :currentTime="currentTime"
      @click.native="showPlayPage = true"
      @update-time="$refs.audio.currentTime = $event"
      :songlist="songlist"
      @updata-song="song = $event"
      @changeSong="changeSong"
      :model="model"
      class="xxx"
      @delete-song="deleteSong"
    />

    <!-- <i class="fa fa-arrow-left" v-show="!fade" @click="fadeInout"></i> -->
  </div>
</template>

<script>
import MusicsControl from "@/components/MusicsControl.vue";
export default {
  components: {
    MusicsControl,
  },
  data() {
    return {
      song: JSON.parse(localStorage.getItem("song")) || null,
      playing: false,
      duration: 0,
      // currentTime: JSON.parse(localStorage.getItem("currentTime"))[0] || 0,
      currentTime: 0,

      showPlayPage: false,

      lyris: [],
      songlist: JSON.parse(localStorage.getItem("songlist")) || [],
      models: ["顺", "随", "单"],
      model: "顺",
    };
  },

  created() {
    this.axios
      .get("https://apis.netstart.cn/music//lyric", {
        params: {
          id: this.song?.id,
        },
      })
      .then((res) => {
        // this.lyris =

        const arr = res.data.lrc.lyric.split("\n");
        var parsed = arr
          .filter((o) => o)
          .map((o) => {
            var { groups } = o?.match(
              /\[(?<mm>\d{2}):(?<ss>\d{2}\.\d{2,3})\](?<text>.*)/
            );
            return {
              time: groups.mm * 60 + groups.ss * 1,
              text: groups.text.trim(),
            };
          });
        // console.log(parsed);
        this.lyris = parsed;
      });
  },
  mounted() {
    this.$refs.audio.currentTime = JSON.parse(
      localStorage.getItem("currentTime")
    ).shift();
  },
  watch: {
    song(newSong) {
      localStorage.setItem("song", JSON.stringify(newSong));
      // this.addSong(newSong);
      this.axios
        .get("https://apis.netstart.cn/music//lyric", {
          params: {
            id: this.song?.id,
          },
        })
        .then((res) => {
          // this.lyris =

          const arr = res.data.lrc.lyric.split("\n");
          var parsed = arr
            .filter((o) => o)
            .map((o) => {
              var { groups } = o.match(
                /\[(?<mm>\d{2}):(?<ss>\d{2}\.\d{2,3})\](?<text>.*)/
              );
              return {
                time: groups.mm * 60 + groups.ss * 1,
                text: groups.text.trim(),
              };
            });
          // console.log(parsed);
          this.lyris = parsed;
        });
    },
    songlist(newSonglist) {
      localStorage.setItem("songlist", JSON.stringify(newSonglist));
    },
    currentTime(newCurrentTime) {
      // console.log(this.$refs.audio.volume + 2);
      // this.$refs.audio.volume = 0.2;
      let arr = [newCurrentTime];
      localStorage.setItem("currentTime", JSON.stringify(arr));
    },
  },

  methods: {
    addSong(obj) {
      // console.log(obj);
      //  if(this.songlist.length){
      var filArr = this.songlist?.filter((o) => o.id !== obj.id);
      filArr.push(obj);
      this.songlist = filArr;
      //  }
      // console.log(filArr.unshift(obj));
    },

    changeSong(direction) {
      // console.log(direction);
      switch (this.model) {
        case "顺":
          var currentSongIndex = this.songlist.findIndex(
            (o) => o.id === this.song.id
          );

          if (direction === "next") {
            this.song =
              currentSongIndex === this.songlist.length - 1
                ? this.songlist[0]
                : this.songlist[currentSongIndex + 1];
          } else {
            this.song =
              currentSongIndex === 0
                ? this.songlist[this.songlist.length - 1]
                : this.songlist[currentSongIndex - 1];
          }
          break;
        case "随":
          var afterfil = this.songlist.filter((o) => o.id !== this.song.id);
          this.song = afterfil[Math.floor(Math.random() * afterfil.length)];
          break;
        case "单":
          this.$refs.audio.currentTime = 0;
          this.$refs.audio.play();
          break;
      }
    },
    changeModel: function () {
      var index = this.models.findIndex((o) => o === this.model);

      this.model =
        index === this.models.length - 1
          ? this.models[0]
          : this.models[index + 1];
    },
    deleteSong(id) {
      // console.log(id);
      var filArr = this.songlist.filter((o) => o.id !== id);
      this.songlist = filArr;
      if (id == this.song.id) {
        this.changeSong("next");
      }
    },
    changeVolume(num) {
      // console.log(num);
      if (num !== "muted") {
        this.$refs.audio.volume = num / 100;
      } else {
        this.$refs.audio.muted = true;
      }
    },
  },
};
</script>
