#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  position: relative;

  audio {
    display: none;
  }

  i.fa-arrow-left {
    display: block;
    width: 20rem;
    height: 20rem;
    background-color: #8f929447;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 16rem;
    position: fixed;
    bottom: 5rem;
    right: 5rem;
    color: #fff;
  }
}
</style>
