<template>
  <div class="newsong">
    <!-- <div class="backwall">
      <img src="@/assets/bg_new@2x.e4f1df4.png" alt="" />
      <div class="text">
        <h1>新歌榜</h1>
        <p>2022-{{ month }}-{{ day }} 更新</p>
      </div>
    </div> -->
    <div class="backwall">
      <div class="text">
        <img src="@/assets/icon_rank_new.png" alt="" />
        <p>2022-{{ month }}-{{ day }} 更新</p>
      </div>
    </div>
    <div class="playall">
      <h1>
        <span> 共{{ newsongs.length }}首歌</span
        ><img
          src="@/assets/playall@3x.77caa0c.png"
          alt=""
          @click="$emit('update-song-list', newsongs)"
        />
      </h1>
    </div>
    <ul v-if="newsongs.length">
      <!-- <NewMusic
        v-for="item in newsongs"
        :item="item"
        :key="item.id"
        @updata-song="$emit('updata-song', $event)"
        :song="song"
        @add-song="$emit('add-song', $event)"
      /> -->

      <li
        v-for="(item, index) in newsongs"
        :key="item.id"
        @updata-song="$emit('updata-song', $event)"
        @add-song="$emit('add-song', $event)"
      >
        <router-link to="/new" class="m-item">
          <span class="num">{{ index + 1 }}</span>
          <div
            class="musicItem"
            @click="
              $emit('updata-song', item);
              $emit('add-song', item);
            "
            :class="{ active: item.id == song?.id }"
          >
            <div class="left">
              <p>{{ item.name }}</p>
              <div class="musicInfo">
                <i v-if="item.song.sqMusic"></i>
                {{ item.song.artists[0].name }} - {{ item.song.album.name }}
              </div>
            </div>
            <div class="right" @click.stop="$emit('add-song', item)">
              <!-- <i v-if="item.id == song?.id" class="yes"
          ><img src="../assets/play.gif" alt=""
        /></i> -->
              <i class="no">
                <img src="@/assets/icon_download@2x.7d2f423.png" alt="" />
              </i>
            </div>
          </div>
        </router-link>
      </li>
    </ul>

    <CopyRight />
  </div>
</template>

<script>
import CopyRight from "@/components/CopyRight.vue";

export default {
  props: {
    song: Object,
  },
  components: {
    CopyRight,
  },
  data() {
    return {
      personalizes: [],
      newsongs: [],
      loading: true,
      month: (new Date().getMonth() + 1).toString().padStart(2, 0),
      day: new Date().getDate().toString().padStart(2, 0),
    };
  },
  created() {
    Promise.all([
      this.axios.get("https://apis.netstart.cn/music/personalized", {
        params: { limit: 6 },
      }),
      this.axios.get("https://apis.netstart.cn/music/personalized/newsong"),
    ]).then(([p, n]) => {
      this.personalizes = p.data.result;
      this.newsongs = n.data.result;
      this.loading = false;
    });
  },
};
</script>

<style lang="less" scoped>
&.active {
  color: #bba504;
}
.backwall {
  width: 100%;
  height: 250rem;
  position: relative;
  background-image: linear-gradient(45deg, #71abcab0, #00ffe066);
  .text {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    width: 200rem;
    height: 200rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    img {
      border-radius: 50%;
    }
    p {
      position: absolute;
      bottom: 20rem;
      left: 50%;
      transform: translateX(-50%);
      color: #fff;
      font-size: 14rem;
      width: 100%;
    }
  }
}
.playall {
  width: 100%;
  position: sticky;
  top: 40rem;
  background-color: #fff;
  h1 {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10rem 10rem;
    font-size: 22rem;
    padding-left: 20rem;
  }
  img {
    height: 40rem;
  }
}
.m-item {
  color: #333;
  display: flex;
  padding-left: 10rem;
  text-decoration: none;
  border-bottom: 1rem solid rgb(233, 232, 232);
  //   width: 375rem;
  color: #333;
  span.num {
    display: inline-block;
    width: 30rem;
    height: 100%;
    text-align: center;
    font-size: 16rem;
    line-height: 45rem;
    padding-right: 10rem;
    color: #888;
  }
  .musicItem {
    flex: 1 1 auto;
    display: flex;
    justify-content: space-between;
    .left {
      padding: 6rem 0;
      //   width: 323rem;
      p {
        width: 90%;
        font-size: 18rem;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        font-weight: 600;
      }
      .musicInfo {
        font-size: 12rem;
        color: #888;
        i {
          display: inline-block;
          width: 12rem;
          height: 8rem;
          line-height: 8rem;
          background: url("@/assets/tag_vip@2x.c6672da.png") no-repeat;
          background-size: 105%;
          background-position: -0rem -2rem;
          margin-right: 5rem;
          line-height: 20rem;
        }
      }
    }
    .right {
      width: 42rem;
      display: flex;
      justify-content: center;
      align-items: center;
      i.no {
        display: block;
        width: 22rem;
        height: 22rem;
        // background: url("../assets/index_icon_2x.png") no-repeat;
        // background-position: -21rem 1rem;
        // background-size: 700%;
        img {
          width: 100%;
          height: 100%;
          display: block;
        }
      }
      i.yes {
        display: block;
        width: 22rem;
        height: 22rem;
        img {
          width: 100%;
        }
      }
    }
  }
}
</style>