<template>
  <router-link to="/hot" class="m-item">
    <div
      class="musicItem"
      @click="
        $emit('updata-song', item);
        $emit('update-song-list');
        clickHandler(item);
      "
    >
      <div class="left">
        <div class="number">{{ index + 1 }}</div>
        <div>
          <p :class="{ active: item.id == song?.id }">{{ item.name }}</p>
          <div class="musicInfo">
            <i v-if="item.sq"></i>
            {{ item.ar[0].name }} - {{ item.al.name }}
          </div>
        </div>
      </div>
      <div class="right">
        <!-- <i v-if="item.id == song?.id" class="yes"
          ><img src="../assets/play.gif" alt=""
        /></i> -->
        <i class="no">
          <img src="@/assets/icon_download@2x.7d2f423.png" alt="" />
        </i>
      </div>
    </div>
  </router-link>
</template>

<script>
export default {
  props: {
    item: Object,
    index: Number,
    song: Object,
  },
  data() {
    return {
      n: 0,
    };
  },
  methods: {
    clickHandler(item) {
      // console.log(123, item);
      this.$emit("add-song", item);
    },
  },
};
</script>
<style lang="less" scoped>
.m-item {
  color: #333;
  display: flex;
  //   padding-left: 10rem;
  text-decoration: none;
  border-bottom: 1rem solid rgb(233, 232, 232);
  //   width: 375rem;
  color: #333;

  .musicItem {
    flex: 1 1 auto;
    display: flex;
    justify-content: space-between;
    .left {
      padding: 6rem 0;
      //   width: 323rem;
      display: flex;
      align-content: center;
      .number {
        height: 100%;
        display: flex;
        align-items: center;
        width: 40rem;
        text-align: center;
        justify-content: center;
        font-size: 17rem;
        color: #888;
      }
      p {
        font-size: 18rem;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        word-break: normal;
        color: #333;
        font-weight: 600;
        &.active {
          color: #bba504;
        }
      }
      .musicInfo {
        font-size: 12rem;
        color: #888;
        i {
          display: inline-block;
          width: 12rem;
          height: 8rem;
          line-height: 8rem;
          background: url("@/assets/tag_vip@2x.c6672da.png") no-repeat;
          background-size: 105%;
          background-position: -0rem -2rem;
          margin-right: 5rem;
          line-height: 20rem;
        }
      }
    }
    .right {
      width: 42rem;
      display: flex;
      justify-content: center;
      align-items: center;
      i.no {
        display: block;
        width: 22rem;
        height: 22rem;

        img {
          width: 100%;
          height: 100%;
          display: block;
        }
      }
      i.yes {
        display: block;
        width: 22rem;
        height: 22rem;
        img {
          width: 100%;
        }
      }
    }
  }
}
</style>