<template>
  <div class="bottom">
    <!-- <div class="volume" v-show="isDetail">
      <i class="volume-icon">
        <img
          src="@/assets/volume.png"
          alt=""
          @click="
            $emit('change-volume', 0), (muted = true), changeVolumeSlider(0)
          "
          v-if="!muted"
        />
        <img
          src="@/assets/volume_close.png"
          alt=""
          v-if="muted"
          @click="
            $emit('change-volume', 100),
              (muted = false),
              changeVolumeSlider(100)
          "
        />
      </i>
      <div class="out">
        <input
          ref="input"
          type="range"
          min="0"
          value="100"
          @input="
            $emit('change-volume', $event.target.value);
            changeVolumeSlider($event.target.value);
          "
        />
        <div class="slider" ref="volumeSlider">
          <i class="dot"></i>
        </div>
      </div>
    </div> -->
    <div class="tip" v-if="show">{{ tests }}</div>
    <div class="like" v-show="!isDetail">
      <img src="@/assets/like-1.png" alt="" />
      <img src="@/assets/download.png" alt="" />
      <img src="@/assets/singing.png" alt="" />
      <img src="@/assets/pinglun.png" alt="" />
      <img src="@/assets/dot.png" alt="" />
    </div>
    <div class="top">
      <span class="currentTime">{{ mm }}:{{ ss }}</span>
      <div class="slider">
        <div class="sliderBar" ref="sliderBar"><i></i></div>
        <input
          type="range"
          :value="tmpCurrentTime"
          min="0"
          :max="duration"
          @change="
            draging = false;
            $emit('update-time', $event.target.value);
          "
          @input="
            (draging = true),
              changeSlider($event.target.value),
              $emit('change-lyric', $event.target.value);
            $emit('update-lyric', $event.target.value);
          "
        />
      </div>
      <span class="duration">{{ m }}:{{ s }}</span>
    </div>
    <div class="icons">
      <div
        class="type"
        @click.stop="
          $emit('changeModel');
          changeType();
        "
      >
        <!-- <i class="fa fa-random"></i> -->
        <!-- <button @click.stop="$emit('changeModel')">{{ model }}</button> -->
        <i v-if="model == '随'">
          <img src="@/assets/24gl-shuffle.png" alt="" />
        </i>
        <i v-else-if="model == '顺'">
          <img src="@/assets/listrepeat.png" alt="" />
        </i>
        <i v-else>
          <img src="@/assets/24gl-repeatOnce2.png" alt="" />
        </i>
      </div>
      <div class="prev">
        <i @click.stop="$emit('changeSong', 'prev')">
          <img src="@/assets/24gl-previous.png" alt="" />
        </i>
      </div>
      <div class="play-pause">
        <i v-if="!playing" @click.stop="$emit('song-play')">
          <img src="@/assets/24gl-playCircle.png" alt="" />
        </i>
        <i v-else @click.stop="$emit('song-pause')">
          <img src="@/assets/24gl-pauseCircle.png" alt="" />
        </i>
      </div>
      <div class="next">
        <i @click.stop="$emit('changeSong', 'next')">
          <img src="@/assets/24gl-next.png" alt="" />
        </i>
      </div>
      <div class="list" @click="$emit('closeSonglist')">
        <i>
          <img src="@/assets/24gl-playlist2.png" alt="" />
        </i>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    currentTime: Number,
    duration: Number,
    playing: Boolean,
    model: String,
    models: Array,
    isDetail: Boolean,
  },
  data() {
    return {
      mm: "00",
      ss: "00",
      m: "00",
      s: "00",
      draging: false,
      tmpCurrentTime: this.currentTime,
      text: "",
      show: false,
      muted: false,
    };
  },
  computed: {
    tests() {
      if (this.model == "随") {
        return "随机播放";
      } else if (this.model == "顺") {
        return "顺序播放";
      } else {
        return "单曲循环";
      }
    },
  },
  methods: {
    changeSlider(w) {
      //   console.log(w);
      this.tmpCurrentTime = w;
      this.$refs.sliderBar.style.width = (w / this.duration) * 100 + "%";
      this.mm = parseInt(this.tmpCurrentTime / 60)
        .toString()
        .padStart(2, 0);
      this.ss = parseInt(this.tmpCurrentTime % 60)
        .toString()
        .padStart(2, 0);

      this.m = parseInt(this.duration / 60)
        .toString()
        .padStart(2, 0);

      this.s = parseInt(this.duration % 60)
        .toString()
        .padStart(2, 0);
    },

    changeType() {
      this.show = true;
      let timer = null;
      // 在 delay 时间内，最多执行一次 func

      if (!timer) {
        timer = setTimeout(() => {
          this.show = false;
          // 完成一次计时，清空，待下一次触发

          timer = null;
        }, 1500);
      }
    },
    changeVolumeSlider(w) {
      this.$refs.volumeSlider.style.width = w + "%";
    },
  },
  mounted() {
    this.$refs.sliderBar.style.width =
      (this.currentTime / this.duration) * 100 + "%";
    this.mm = parseInt(this.tmpCurrentTime / 60)
      .toString()
      .padStart(2, 0);
    this.ss = parseInt(this.tmpCurrentTime % 60)
      .toString()
      .padStart(2, 0);
    this.m = parseInt(this.duration / 60)
      .toString()
      .padStart(2, 0);
    this.s = parseInt(this.duration % 60)
      .toString()
      .padStart(2, 0);
  },

  watch: {
    currentTime(n) {
      if (!this.draging) {
        this.tmpCurrentTime = n;
        this.$refs.sliderBar.style.width = (n / this.duration) * 100 + "%";
      }
      this.mm = parseInt(this.tmpCurrentTime / 60)
        .toString()
        .padStart(2, 0);
      this.ss = parseInt(this.tmpCurrentTime % 60)
        .toString()
        .padStart(2, 0);
      this.m = parseInt(this.duration / 60)
        .toString()
        .padStart(2, 0);
      this.s = parseInt(this.duration % 60)
        .toString()
        .padStart(2, 0);
    },
  },
};
</script>

<style lang="less" scoped>
.bottom {
  width: 100%;
  height: 150rem;
  color: #fff;
  box-sizing: border-box;
  padding: 10rem;
  overflow: hidden;
  display: flex;
  justify-content: flex-end;
  flex-direction: column;
  position: relative;
  .tip {
    position: absolute;
    left: 50%;
    top: 10rem;
    transform: translateX(-50%);
    width: 50rem;
    padding: 5rem 10rem;
    background-color: #fff;
    color: #000;
    border-radius: 15rem;
  }
  .volume {
    width: 100%;
    position: relative;
    height: 60rem;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    i.volume-icon {
      position: relative;
      display: block;
      width: 30rem;
      height: 30rem;
      // background-color: #000;
      margin-right: 12rem;
      margin-left: 10rem;
      img {
        position: absolute;
        width: 100%;
        height: 100%;
      }
    }

    .out {
      width: 50%;
      position: relative;

      // left: 50%;
      // transform: translateX(-50%);
      height: 2rem;
      background-color: rgb(149, 145, 145);
      input {
        position: absolute;
        height: 5rem;
        width: 100%;
        opacity: 0;
      }
      .slider {
        // width: 50%;
        height: 100%;
        background-color: #fff;
        position: relative;
        i.dot {
          position: absolute;
          display: block;
          width: 5rem;
          height: 5rem;
          background-color: #fff;
          top: -2rem;
          border-radius: 50%;
          right: 0;
        }
      }
    }
  }

  .like {
    width: 100%;
    height: 50rem;
    display: flex;
    justify-content: space-around;
    align-items: center;
    img {
      display: block;
      width: 25rem;
      height: 25rem;
    }
  }
  .top {
    display: flex;
    justify-content: space-around;
    align-items: center;
    span {
      font-size: 12rem;
    }
    span.currentTime {
      color: #fff;
    }
    span.duration {
      color: rgb(217, 214, 214);
    }
    .slider {
      position: relative;
      width: 250rem;
      height: 1rem;
      background-color: rgb(165, 160, 160);

      .sliderBar {
        height: 100%;
        position: absolute;
        left: 0;
        background-color: rgb(253, 250, 250);
        i {
          display: block;
          width: 5rem;
          height: 5rem;
          background-color: #fff;
          position: absolute;
          right: -2rem;
          top: -2rem;
          border-radius: 50%;
        }
      }
      input {
        position: absolute;
        display: block;
        left: 0;
        width: 100%;
        z-index: 999;
        height: 100%;
        opacity: 0;
        // height: 100%;
      }
    }
  }
  .icons {
    display: flex;
    justify-content: space-around;
    // padding: 20rem 0;
    height: 50rem;
    padding: 10rem;
    div {
      width: 50rem;
      height: 50rem;
      display: flex;
      justify-content: center;
      align-items: flex-start;

      i {
        display: block;
        // width: 100%;
        // height: 100%;
        width: 25rem;
        height: 50rem;
        font-size: 24rem;
        display: flex;
        justify-content: center;
        align-items: center;
        img {
          width: 100%;
          display: block;
        }
      }
      &.play-pause {
        position: relative;
        width: 35rem;
        // height: 40rem;
        i {
          width: 50rem;
          position: absolute;

          font-size: 30rem;
        }
      }
    }
  }
}
</style>