<template>
  <div>
    <div class="form">
      <div class="search-box">
        <i class="search"></i>
        <input
          type="text"
          placeholder="搜索你想听的歌曲"
          v-model="value"
          @keyup.enter="
            addHis($event.target.value), searchDetail($event.target.value)
          "
          ref="input"
          @focus="isDetailSearch = true"
          @blur="searchDetail($event.target.value)"
        />
        <i class="close" @click="emptyInput" v-show="value != ''"></i>
      </div>
      <span>
        <router-link to="/"> 取消 </router-link>
      </span>
    </div>

    <HotSearch
      :hots="hots"
      v-show="value == '' && isDetailSearch"
      :todos="todos"
      @addHis="addHis($event)"
      @deletehisSearch="deletehisSearch($event)"
      @searchDetail="searchDetail($event)"
    />
    <SearchNow
      v-show="value != '' && isDetailSearch"
      :searches="searches"
      :value="value"
      @suggestDetail="searchDetail($event)"
      @addHis="addHis($event)"
    />
    <SuggestDetail
      v-show="!isDetailSearch"
      :album="album"
      :songs="songs"
      :song="song"
      @updata-song="$emit('updata-song', $event)"
      @add-song="$emit('add-song', $event)"
    />
  </div>
</template>

<script>
import HotSearch from "@/components/search/HotSearch.vue";
import SearchNow from "@/components/search/SearchNow.vue";
import SuggestDetail from "@/components/search/SuggestDetail.vue";

import _ from "lodash";

export default {
  components: {
    HotSearch,
    SearchNow,
    SuggestDetail,
  },
  props: {
    song: Object,
  },
  data() {
    return {
      hots: [],
      value: "",
      //    //  对搜索做一个防抖
      debounceGetSearchInfo: _.debounce(this.getSearchInfo, 500),
      searches: [],
      isDetailSearch: true,
      // isFocus: true,
      todos: JSON.parse(localStorage.getItem("hisSearches")) || [],

      album: {},
      songs: [],
    };
  },
  methods: {
    getSearchInfo() {
      this.axios
        .get("https://apis.netstart.cn/music/search/suggest", {
          params: {
            keywords: this.value,
            type: "mobile",
          },
        })
        .then((res) => {
          // console.log(res);
          this.searches = res?.data?.result?.allMatch;
        });
    },
    emptyInput() {
      this.value = "";
    },

    addHis(text) {
      // this.value = "";
      if (text.trim()) {
        this.todos = this.todos.filter((o) => {
          return o.first != text;
        });
        this.todos.unshift({
          id: this.todos.length
            ? Math.max(...this.todos.map((o) => o.id)) + 1
            : 1,
          first: text,
        });
        localStorage.setItem("hisSearches", JSON.stringify(this.todos));
      }
    },
    deletehisSearch({ id }) {
      // console.log(text, id);
      // console.log(id);
      this.todos = this.todos.filter((o) => {
        return o.id != id;
      });
      localStorage.setItem("hisSearches", JSON.stringify(this.todos));
    },

    searchDetail(value) {
      // console.log(value);
      this.loading = true;
      if (value.trim()) {
        this.value = value;
        this.isDetailSearch = false;
        Promise.all([
          this.axios.get("https://apis.netstart.cn/music/search/multimatch", {
            params: {
              keywords: value,
            },
          }),
          this.axios.get("https://apis.netstart.cn/music/cloudsearch", {
            params: {
              keywords: value,
            },
          }),
        ]).then(([a, b]) => {
          this.loading = false;
          this.album = a?.data?.result;
          this.songs = b?.data?.result?.songs;
          // console.log(a);
          // console.log(b);
        });
      }
    },
  },
  created() {
    this.axios.get("https://apis.netstart.cn/music/search/hot").then((res) => {
      //   console.log(res);
      this.hots = res.data.result.hots;
    });
  },
  mounted() {
    this.$nextTick(() => {
      this.$refs.input.focus();
    });
  },
  watch: {
    value(newValue) {
      // newValue ? (this.value=='') : (this.whichShow = true);
      if (newValue.trim()) {
        this.debounceGetSearchInfo();
      } else {
        this.songs = [];
        this.album = {};
        this.isDetailSearch = true;
      }
    },
    todos() {
      this.getHisSearch = JSON.parse(localStorage.getItem("hisSearches"));
    },
  },
};
</script>

<style lang="less" scoped>
.form {
  padding: 10rem 10rem;
  //   border-bottom: 1rem solid rgb(233, 232, 232);
  box-shadow: 0 0 2rem 0rem rgb(233, 232, 232);
  display: flex;
  align-items: center;
  span {
    display: inline-block;
    padding: 3rem 5rem;
    a {
      color: rgb(173, 173, 173);
      text-decoration: none;
      font-size: 16rem;
    }
  }
  .search-box {
    width: 280rem;
    height: 40rem;
    padding-left: 10rem;
    line-height: 18rem;
    // background: rgba(231, 229, 229, 0.978);
    background-color: rgba(215, 215, 215, 0.2);
    font-size: 14rem;
    color: #333;
    border-radius: 30rem;
    display: flex;
    justify-content: center;
    align-items: center;
    // border: 1rem solid;

    i.search {
      display: inline-block;
      width: 15rem;
      height: 15rem;

      margin-right: 5rem;
      background: url("@/assets/search@2x.a3b9c58.png");
      background-size: cover;
      background-position: left top;
    }
    i.close {
      display: inline-block;
      width: 18rem;
      height: 15rem;
      background-color: red;
      margin-right: 5rem;
      background: url("@/assets/close3.png");
      background-size: cover;
      background-position: left top;
    }
    input {
      outline: none;
      border: none;
      height: 100%;
      width: 300rem;
      //   background: rgba(224, 220, 220, 0.978);
      background: transparent;
      font-size: 14rem;
    }
  }
}
</style>