<template>
  <div class="black">
    <div class="songlist">
      <h2>
        <div>
          当前播放<span>({{ songlist.length }})</span>
        </div>
        <i @click="$emit('closeSonglist')">
          <img src="@/assets/close.svg" alt="" />
        </i>
      </h2>
      <li
        v-for="(item, index) in songlist"
        :key="index"
        :style="{ color: item.id === song.id ? '#bba504' : '' }"
      >
        <i class="icon" v-if="item.id === song.id">
          <div :class="{ playing: playing }" class="newsong-play active">
            <span></span>
            <span></span>
            <span></span>
            <span></span>
          </div>
        </i>
        <p @click.stop="$emit('updata-song', item)">
          {{ item.name }}
          <span v-if="item?.ar">-{{ item?.ar[0]?.name }}</span>
          <span v-else>-{{ item?.song?.artists[0]?.name }}</span>
        </p>
        <i @click.stop="$emit('delete-song', item.id)">
          <img src="@/assets/close-o.svg" alt="" />
        </i>
      </li>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    songlist: Array,
    song: Object,
    playing: Boolean,
  },
};
</script>

<style lang="less" scoped>
@keyframes playingsong {
  from {
    transform: rotateX(15deg);
  }

  to {
    transform: rotateX(60deg);
  }
}

.newsong-play {
  display: flex;
  justify-content: space-around;
  width: 22rem;
  height: 18rem;

  &.playing {
    span {
      animation-play-state: running;
    }
  }

  span {
    width: 11%;
    height: 100%;
    background: #ffe31b;
    animation: playingsong 0.8s infinite alternate;
    animation-play-state: paused;
    transform-origin: bottom;

    &:nth-child(1) {
      animation-delay: 0s;
    }

    &:nth-child(2) {
      animation-delay: -0.2s;
    }

    &:nth-child(3) {
      animation-delay: -0.4s;
    }

    &:nth-child(4) {
      animation-delay: -0.6s;
    }
  }
}

.black {
  width: 100%;
  height: 800rem;
  background-color: #150b0b6b;
  z-index: 999999 !important;
}
.songlist {
  position: absolute;
  // padding: 10rem;
  margin: 10rem;
  width: 95%;
  bottom: 85rem;
  box-sizing: border-box;
  background-color: rgb(255, 255, 255);
  border-radius: 20rem;
  height: 60vh;
  color: rgb(10, 10, 10);
  // bottom: 80rem;
  overflow: auto;
  z-index: 99999;
  h2 {
    font-size: 22rem;
    display: flex;
    background-color: rgb(245, 239, 239);
    justify-content: space-between;
    position: sticky;
    // height: 30rem;
    padding: 15rem;
    font-weight: 600;
    top: 0;
    border-bottom: 1px solid rgb(212, 209, 209);
    // letter-spacing: 2rem;
    padding-right: 10rem;
    span {
      font-size: 15rem;
      font-weight: 400;
      color: rgb(156, 152, 152);
    }
    i {
      display: block;
      width: 30rem;
      height: 30rem;
      img {
        width: 100%;
      }
    }
  }
  li {
    margin: 0;
    padding: 0;
    list-style: none;
    font-size: 18rem;
    display: flex;
    justify-content: space-around;
    padding: 10rem 15rem;
    background-color: rgb(255, 255, 255);

    p {
      flex: 1;
      // padding-left: 20rem;
      span {
        font-size: 14rem;
        color: rgb(155, 153, 153);
      }
    }
    i {
      color: #333;
      display: block;
      width: 15rem;
      height: 15rem;
      img {
        width: 100%;
      }
    }
    i.icon {
      margin-right: 10rem;
      img {
        width: 100%;
      }
    }
  }
}
</style>