<template>
  <router-link
    @click="clickHandler"
    class="share-item"
    :to="`/playlist?id=${item.id}`"
  >
    <img :src="`${item.picUrl}?imageView=1&type=webp&thumbnail=123x0`" alt="" />
    <p>{{ item.name }}</p>
    <span
      ><i> <img src="@/assets/icon11.png" alt="" /> </i>{{ hearNums }}</span
    >
  </router-link>
</template>

<script>
export default {
  props: {
    item: Object,
  },
  data() {
    return {
      hearNums:
        this.item.playCount > 100000000
          ? (this.item.playCount / 1000000000).toFixed(1) + "亿"
          : (this.item.playCount / 10000).toFixed(1) + "万",
    };
  },
  methods: {
    clickHandler() {
      console.log(123);
    },
  },
};
</script>

