<template>
  <div class="main">
    <div class="up">
      <SongCD
        :song="song"
        :playing="playing"
        @song-pause="$emit('song-pause')"
        @song-play="$emit('song-play')"
        :isDetail="isDetail"
        @change-volume="$emit('change-volume', $event)"
      />
      <LyrisShort :currentTime="currentTime" playing:="playing" :lyris="lyris"
      :lyricTime="lyricTime" :isDetail="isDetail" @isDetail="isDetail=!isDetail"
      />
      <MusicControl
        :currentTime="currentTime"
        :duration="duration"
        :playing="playing"
        @update-time="$emit('update-time', $event)"
        @update-lyric="lyricTime = $event"
        @song-pause="$emit('song-pause')"
        @song-play="$emit('song-play')"
        :model="model"
        :models="models"
        @changeSong="$emit('changeSong', $event)"
        @changeModel="$emit('changeModel')"
        :isDetail="isDetail"
        @closeSonglist="clickHandler"
        @change-volume="$emit('change-volume', $event)"
      />
    </div>
    <SongsList
      v-show="isShow"
      :songlist="songlist"
      @updata-song="$emit('updata-song', $event)"
      :song="song"
      @delete-song="$emit('delete-song', $event)"
      @closeSonglist="clickHandler"
      class="list"
      :playing="playing"
    />

    <img v-if="song?.picUrl" :src="song?.picUrl" alt="" />
    <img v-else :src="song?.al?.picUrl" alt="" />
  </div>
</template>

<script>
import SongCD from "@/components/songs/SongCD.vue";
import LyrisShort from "@/components/songs/LyrisShort.vue";
import MusicControl from "@/components/songs/MusicControl.vue";
import SongsList from "@/components/SongsList.vue";
export default {
  data() {
    return {
      lyricTime: 0,
      isDetail: false,
      isShow: false,
    };
  },
  methods: {
    clickHandler() {
      this.isShow = !this.isShow;
    },
  },
  components: {
    SongCD,
    LyrisShort,
    MusicControl,
    SongsList,
  },
  props: {
    song: Object,
    songlist: Array,
    currentTime: Number,
    duration: Number,
    playing: Boolean,
    lyris: Array,
    model: String,
    models: Array,
  },
};
</script>

<style lang="less" scoped>
.main {
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  z-index: 9;
  .up {
    background-color: rgba(0, 0, 0, 0.356);
    position: absolute;
    z-index: 9;
    width: 100%;
    .list {
      position: absolute;
      z-index: 99999 !important;
    }
  }
  img {
    position: absolute;
    width: 100%;
    height: 100%;
    display: block;
    filter: blur(30px);
    transform: scale(1.5);
  }
}
</style>