import Vue from 'vue';
import VueRouter from 'vue-router';
import HomePage from '../views/HomePage.vue';
import HomeView from '../views/home/HomeView.vue';
import HotView from '../views/home/HotView.vue';
import NewMusic from "@/components/NewMusic.vue";
import SearchView from '../views/home/SearchView.vue';
import PlayList from '../views/PlayList.vue';
import SongsPage from '../views/SongsPage.vue';
Vue.use(VueRouter);

const routes = [
  // {
  //   path: '/',

  //   component: HomeView,
  //   meta: { showNav: true }
  // },
  // {
  //   path: '/hot',
  //   component: HotView,
  //   meta: { showNav: true }

  // },
  // {
  //   path: '/search',
  //   component: SearchView,
  //   meta: { showNav: true }
  // },

  {
    path: "/",
    component: HomePage,
    meta: { showControls: true },
    children: [
      {
        path: '',
        component: HomeView,
        meta: { showControls: true },
      },
      {
        path: 'hot',
        component: HotView,
        meta: { showControls: true },
      },

      {
        path: 'search',
        component: SearchView,
        meta: { showControls: true },
      },
      {
        path: 'new',
        component: NewMusic,
        meta: { showControls: true },
      }
    ],
  },
  {
    path: '/playlist',
    component: PlayList,
    meta: { showControls: true }
  },

  {
    path: '/songs',
    component: SongsPage,
    meta: { showControls: false }
  }


];

const router = new VueRouter({
  routes
});

export default router;
