<template>
  <div class="lyrisshort" @click="$emit('isDetail')">
    <div class="box" :class="{ isDetail: isDetail }" ref="box">
      <ul ref="ul">
        <li
          ref="lis"
          v-for="(item, index) in lyris"
          :key="index"
          :class="{
            active: findCurrentLyric(lyris, currentTime).index == index,
          }"
        >
          <span
            :style="`animation-duration:${
              lyris[index + 1]?.time - lyris[index]?.time
            }s`"
            >{{ item?.text }}</span
          >
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    currentTime: Number,
    playing: Boolean,
    lyris: Array,
    lyricTime: [String, Number],
    isDetail: Boolean,
  },
  data() {
    return {
      lisheight: [],
    };
  },

  methods: {
    findCurrentLyric(lyris, currentTime) {
      var copy = [...lyris].reverse();
      var index = copy.findIndex((o) => o.time < currentTime);
      return {
        index: copy.length - 1 - index,
        text: copy[index]?.text,
      };
    },

    collectLiHeight() {
      var lis = [...this.$refs.lis];
      var lisHeight = [0];

      lis.forEach((li, i) => {
        lisHeight.push(lisHeight[i] + li.offsetHeight);
      });
      this.lisheight = lisHeight;
      // console.log(lisHeight);
      // return lisHeight;
    },
    findAcitive() {
      this.$refs.lis.forEach((li, i) => {
        // preIndex = i;
        // var preIndex = 0;

        // if (li.className == "active") {
        //   // lyrisHeight = li.offsetHeight;

        //   // console.log(this.$refs.ul.offsetTop);
        //   // 定死高度
        //   // this.$refs.ul.style.top = 110 + (i + 1) * -21 + "rem";

        //   // 跟随歌词高度移动
        //   this.$refs.ul.style.top = 110 + -this.lisheight[i] + "rem";
        //   // console.log(this.lisheight[i]);
        //   // console.log(i);
        // }
        if (li.className == "active") {
          // lyrisHeight = li.offsetHeight;
          if (this.isDetail) {
            // console.log(123);
            this.$refs.ul.style.top = 220 - this.lisheight[i] + "rem";
          } else {
            this.$refs.ul.style.top = 40 - this.lisheight[i] + "rem";
          }
        }
      });
    },
  },

  mounted() {
    this.findCurrentLyric(this.lyris, this.currentTime);
    this.findAcitive();
    this.collectLiHeight();
    // this.lisheight = this.collectLiHeight();
  },

  watch: {
    lyricTime(lyricTime) {
      this.$refs.lis?.forEach((li, i) => {
        if (li.className == "active") {
          // lyrisHeight = li.offsetHeight;
          if (this.isDetail) {
            // console.log(123);
            this.$refs.ul.style.top = 220 - this.lisheight[i] + "rem";
          } else {
            this.$refs.ul.style.top = 40 - this.lisheight[i] + "rem";
          }
        }

        if (this.findCurrentLyric(this.lyris, lyricTime).index == i) {
          li.className = "active";
        } else {
          li.className = "";
        }
      });
    },
    currentTime() {
      // console.log(this.$refs.ul.scrollTop);
      // console.log(this.$refs.box.offsetHeight, this.$refs.box.scrollTop);
      this.findAcitive();
      this.collectLiHeight();
    },
  },
};
</script>

<style lang="less" scoped>
@keyframes jianbian {
  from {
    background-size: 0 100%;
  }

  to {
    background-size: 100% 100%;
  }
}
.box {
  height: 100rem;
  // overflow: auto;
  overflow: hidden;

  position: relative;
  &.isDetail {
    height: 430rem;
  }
  ul {
    width: 100%;
    // height: 100%;
    text-align: center;
    position: absolute;
    top: 110rem;
    transition: all 0.1s;
    li {
      // color: rgb(186, 181, 181);

      span {
        background: #fff -webkit-linear-gradient(left, #ffe31b, #ffe31b) no-repeat
          0 0;

        -webkit-text-fill-color: transparent;

        -webkit-background-clip: text;

        background-size: 0 100%;
      }

      font-size: 14rem;
      // transition: all 0.2s;
      // height: 21rem;
      font-weight: 400;
      padding: 8rem 0rem;
      letter-spacing: 1rem;
      // margin: ;
    }
    li.active {
      color: #fff;
      // font-weight: 600;
      // font-size: 16rem;
      span {
        background-size: 100% 100%;

        animation: jianbian linear;
      }
    }
  }
}
</style>