<template>
  <div class="main">
    <img src="@/assets/play.gif" alt="" class="loading" v-if="loading" />
    <a @click="$router.back()" class="back"
      ><i class="fa fa-angle-left"></i> 歌单</a
    >
    <PlaylistTop v-if="playlist" :playlist="playlist" />
    <SongList
      :playlist="playlist"
      @updata-song="$emit('updata-song', $event)"
      @update-song-list="$emit('update-song-list', playlist?.tracks)"
      :song="song"
    />
    <NiceComment :comments="comments" />
    <CopyRight />
  </div>
</template>

<script>
import PlaylistTop from "@/components/playlist/PlaylistTop.vue";
import SongList from "@/components/playlist/SongList.vue";
import NiceComment from "@/components/playlist/NiceComment.vue";
import CopyRight from "@/components/CopyRight.vue";

export default {
  components: {
    PlaylistTop,
    SongList,
    NiceComment,
    CopyRight,
  },
  props: {
    song: Object,
  },

  data() {
    return {
      playlist: {},
      comments: {},
      loading: true,
    };
  },
  methods: {
    getPlaylist(id) {
      Promise.all([
        this.axios.get("https://apis.netstart.cn/music/playlist/detail", {
          params: { id },
        }),

        this.axios.get("https://apis.netstart.cn/music/comment/playlist/", {
          params: { id },
        }),
      ]).then(([a, b]) => {
        this.playlist = a.data.playlist;
        this.comments = b.data;
        this.loading = false;
      });
    },
  },

  created() {
    this.getPlaylist(this.$route.query.id);
  },
  // beforeRouteUpdate(to, from, next) {
  //   console.log(this.$route.query.id);
  //   next();
  //   // ...
  // },
};
</script>

<style lang="less" socped>
img.loading {
  position: absolute;
  top: 150%;
  left: 50%;
  z-index: 88;
  transform: translateX(-50%);
}
a.back {
  display: block;
  // width: 50rem;
  height: 30rem;
  // background-color: rgb(155, 148, 148);
  position: absolute;
  z-index: 9999;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  font-size: 16rem;
  color: #000;
  line-height: 30rem;
  padding-left: 10rem;
}
.fa-angle-left {
  font-size: 30rem;
  color: #000;
  display: inline-block;
  margin-right: 10rem;
}
</style>