<template>
  <div class="main">
    <SongsList
      :songlist="songlist"
      v-if="list && this.$route.meta.showControls"
      :class="{ active: list && this.$route.meta.showControls }"
      @updata-song="$emit('updata-song', $event)"
      :song="song"
      @delete-song="$emit('delete-song', $event)"
      @closeSonglist="closeSonglist"
      :playing="playing"
    />
    <div class="box" v-show="$route.meta.showControls">
      <!-- <h1>111</h1> -->
      <router-link
        :to="{
          path: '/songs',
          query: {
            pathA: $route.fullPath,
          },
        }"
        class="pic"
      >
        <!-- :src="`${song.picUrl}?imageView=1&type=webp&thumbnail=30x30`" -->
        <img
          v-if="songs?.picUrl"
          :src="`${songs.picUrl}?imageView=1&type=webp&thumbnail=60x60`"
          alt=""
          :class="{ active: playing }"
        />
        <img
          :class="{ active: playing }"
          v-else
          :src="`${songs?.al?.picUrl}?imageView=1&type=webp&thumbnail=60x60`"
          alt=""
        />

        <canvas ref="canvas" width="70" height="70"></canvas>
      </router-link>
      <div class="right">
        <!-- <div class="slide">
          <input
            type="range"
            ref="input"
            name=""
            id=""
            min="0"
            :max="duration"
            :value="tmpCurrentTime"
            @input="(draging = true), changeSlider($event.target.value)"
            @change="
              draging = false;
              $emit('update-time', $event.target.value);
            "
          />
          <div class="inner" ref="slider"></div>
        </div> -->
        <div class="controls">
          <div class="controlBar">
            <div class="info">
              <h2>{{ song?.name }}</h2>
              <p v-if="song?.ar">{{ song?.ar[0]?.name }}</p>
              <p v-else>
                {{ song?.song?.artists[0]?.name }}
              </p>
              <!--   <p class="artist" v-if="song?.ar">
          {{ song?.ar[0]?.name }}<i class="fa fa-angle-right"></i>
        </p>
        <p class="artist" v-else>
          {{ song?.song?.artists[0]?.name }}<i class="fa fa-angle-right"></i>
        </p> -->
            </div>
            <!-- <i @click.stop="$emit('changeSong', 'prev')">
              <img src="@/assets/24gl-previous.png" alt="" />
            </i> -->
            <i class="middle">
              <i v-if="!playing" @click.stop="$emit('song-play')">
                <img src="@/assets/24gl-playCircle.png" alt="" />
              </i>
              <i v-else @click.stop="$emit('song-pause')">
                <img src="@/assets/24gl-pauseCircle.png" alt="" />
              </i>
            </i>
            <!-- <i @click.stop="$emit('changeSong', 'next')">
              <img src="@/assets/24gl-next.png" alt="" />
            </i> -->
            <i @click="list = !list">
              <img src="@/assets/24gl-playlist2.png" alt="" />
            </i>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SongsList from "@/components/SongsList.vue";
export default {
  components: {
    SongsList,
  },

  props: {
    song: Object,
    currentTime: Number,
    duration: Number,
    playing: Boolean,

    songlist: Array,
    model: [String],
  },

  data() {
    return {
      list: false,
      showDisc: false,
      draging: false,
      tmpCurrentTime: this.currentTime || 0,
    };
  },
  mounted() {
    // this.$refs.slider.style.width =
    //   (this.currentTime / this.duration) * 100 + "%";
  },
  computed: {
    songs() {
      return this.song;
    },

    percentage() {
      return this.currentTime / this.duration;
    },
  },
  watch: {
    // currentTime: function (n) {
    //   //   console.log(n);
    //   if (!this.draging) {
    //     this.tmpCurrentTime = n;
    //     this.$refs.slider.style.width = (n / this.duration) * 100 + "%";
    //   }
    // },
    percentage(n) {
      // console.log(n);
      var canvas = this.$refs.canvas;
      // console.log(canvas);
      var context = canvas.getContext("2d");
      context.clearRect(0, 0, 70, 70);
      context.beginPath();
      context.arc(35, 35, 32, -0.5 * Math.PI, Math.PI * (2 * n - 0.5));
      // context.closePath();
      context.strokeStyle = "#fff";
      context.lineWidth = "3";
      context.stroke();
    },
  },

  methods: {
    changeSlider(value) {
      //   console.log(value);
      this.$refs.slider.style.width = (value / this.duration) * 100 + "%";
    },
    closeSonglist() {
      // console.log(123);
      this.list = false;
    },
  },
};
</script>

<style lang="less" scoped>
.main {
  position: fixed;

  bottom: -2rem;
  z-index: 2;
  width: 100%;
  // height: 100%;

  .box {
    height: 60rem;
    background-color: rgb(17, 16, 16);
    display: flex;
    justify-content: space-between;
    box-sizing: border-box;
    padding-bottom: 5rem;

    // width: 100%;
    position: relative;
    bottom: 0;
    width: 100%;

    align-items: center;
    padding: 0 10rem;

    .pic {
      width: 60rem;
      height: 60rem;
      border-radius: 50%;
      position: relative;
      top: -10rem;
      overflow: hidden;
      padding: 3rem;
      box-shadow: 0 0 5rem 0rem gray;
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: black;
      canvas {
        position: absolute;
        // left: 0;
        // top: 0;
        // width: 75rem;
        // height: 75rem;
        // z-index: 9;
      }
      img {
        width: 100%;
        border-radius: 50%;
        animation: rotater 5s linear infinite paused;
      }

      // animation-direction: alternate-reverse;
      // animation-iteration-count: infinite;
    }
    img.active {
      animation: rotater 5s linear infinite;
    }
    @keyframes rotater {
      from {
        transform: rotate(0deg);
      }
      to {
        transform: rotate(360deg);
      }
    }
    .right {
      position: relative;
      flex: 1;
      margin-left: 20rem;
      height: 60rem;
      // background-color: pink;
      padding: 5rem;
      .slide {
        background-color: #bbbdc04a;
        position: relative;
        width: 255rem;
        height: 15rem;
        box-sizing: border-box;
        display: flex;
        align-items: center;
        border-radius: 15rem;
        padding: 0 2rem;
        input {
          display: block;
          width: 100%;
          position: absolute;
          z-index: 22;
          // height: 100%;
          opacity: 0;
        }
        .inner {
          // width: 20%;
          width: 0%;
          height: 10rem;
          background-color: #fff;
          border-radius: 10rem;
        }
      }
      .controls {
        width: 250rem;
        height: 40rem;
        display: flex;
        align-items: center;
        margin-top: 10rem;
        .controlBar {
          overflow: hidden;
          width: 102%;
          background-color: #0a0a0b00;
          height: 40rem;
          border-radius: 10rem;
          display: flex;
          align-items: center;
          justify-content: space-between;
          padding: 5rem;
          // border: 1rem solid #fff;
          .info {
            flex: 0.7;
            // background-color: pink;

            h2 {
              overflow: hidden;
              white-space: nowrap;
              text-overflow: ellipsis;
              width: 94rem;
              font-weight: 400;
              font-size: 16rem;
              color: #fff;
              padding-left: 5rem;
            }
            p {
              overflow: hidden;
              white-space: nowrap;
              width: 94rem;
              text-overflow: ellipsis;
              color: #bbbdc0ef;
              font-size: 12rem;
              padding-left: 5rem;
            }
          }

          i {
            display: block;
            width: 30rem;
            height: 30rem;
            font-size: 18rem;
            line-height: 30rem;
            text-align: center;
            // background-color: rgba(255, 255, 255, 0.468);
            border-radius: 50%;
            display: flex;
            justify-content: center;
            align-items: center;
            // margin-left: 5rem;
            img {
              width: 100%;
              height: 100%;
            }
          }
          i.middle {
            position: relative;
            // margin: 0 10rem;
            width: 30rem;
            height: 30rem;

            i {
              display: block;
              width: 100%;
              height: 100%;
              img {
                width: 100%;
                height: 100%;
              }
            }
          }
          i.fa-close {
            display: block;
            // width: 20rem;
            // height: 20rem;
            height: 25rem;
            background-color: rgb(247, 242, 242);
            color: rgb(39, 37, 37);

            border-radius: 5rem;
            position: absolute;
            right: -10rem;
            top: 5rem;
          }
        }
      }
    }
  }
}
</style>