<template>
  <div>
    <img src="@/assets/play.gif" alt="" class="loading" v-if="loading" />
    <!-- <div class="banner">
      <div class="yun"></div>
      <h3>更新日期: {{ month }}月{{ day }}日</h3>
    </div> -->
    <div class="backwall">
      <div class="text">
        <img src="@/assets/icon_rank_hot.png" alt="" />
        <p>2022-{{ month }}-{{ day }} 更新</p>
      </div>
    </div>
    <div class="playall">
      <h1>
        <span> 共{{ hotMusics.length }}首歌</span
        ><img
          src="@/assets/playall@3x.77caa0c.png"
          alt=""
          @click="$emit('update-song-list', hotMusics)"
        />
      </h1>
    </div>
    <div class="hotmusicItem">
      <HotMusic
        v-for="(item, index) in hotMusics"
        :key="item.id"
        :item="item"
        :index="index"
        @updata-song="$emit('updata-song', $event)"
        :song="song"
        @add-song="$emit('add-song', $event)"
      />
    </div>
  </div>
</template>

<script>
import HotMusic from "@/components/HotMusic.vue";
export default {
  props: {
    song: Object,
  },
  components: {
    HotMusic,
  },
  methods: {
    // Viewshow(e) {
    //   console.log(1234);
    //   this.$emit("add-song", e);
    // },
  },
  data() {
    return {
      month: (new Date().getMonth() + 1).toString().padStart(2, 0),
      day: new Date().getDate().toString().padStart(2, 0),
      hotMusics: [],
      loading: true,
    };
  },
  created() {
    this.axios
      .get("https://apis.netstart.cn/music/playlist/detail?id=3778678")
      .then((res) => {
        // console.log(res.data.playlist.tracks);
        this.hotMusics = res.data.playlist.tracks;
        this.loading = false;
      });
  },
};
</script>

